import * as React from "react"

import Header from "./header"
import Footer from "./footer"
import "./layout.css"
import "./styles.css"

const Layout = ({ children }) => {
  return (
    <>
      <Header siteTitle={`KIA MOTORS`} />
      <main className="container-sm">
        {children}
      </main>
      <Footer />
    </>
  )
}

export default Layout
