import { graphql, useStaticQuery } from "gatsby"
import * as React from "react"

import Layout from "../components/layout"

const CatalogPage = () => {
    const result = useStaticQuery(
        graphql`
            {
                allDatoCmsCategory {
                    nodes {
                    originalId
                    titulo
                    }
                }
                allDatoCmsVehicle {
                    nodes {
                        originalId
                        nombre
                        categoria {
                            originalId
                        }
                        imagen {
                            url
                        }
                    }
                }
            }
        `
    );
    return (
        < Layout >
            <div className="row">
                <div className="col-lg-12 text-center my-3">
                    <h1>SELECCIONE UN VEHÍCULO</h1>
                </div>
            </div>
            <div className="row text-center d-flex justify-content-center">
                {result.allDatoCmsCategory.nodes.map(category => (
                    <div className="col-lg-2 bg-dark rounded mx-2 my-4">
                        <h3 className="h3 text-light text-uppercase" key={category.originalId}>{category.titulo}</h3>
                        {result.allDatoCmsVehicle.nodes.map(vehicle => {
                            if (category.originalId === vehicle.categoria.originalId) {
                                return <div className="row">
                                    <div className="col-lg-12 col-sm-12 bg-light">
                                        <h5 className="h5 text-dark text-uppercase" key={vehicle.originalId}>{vehicle.nombre}</h5>
                                        <a href={"/JAMSTACK/" + vehicle.nombre}>
                                            <img src={vehicle.imagen.url} width="100%" height="150" alt="N/A" />
                                        </a>
                                    </div>
                                </div>
                            } else {
                                return null;
                            }
                        }
                        )}
                    </div>
                ))}
            </div>
            <div className="row">
                <div className="col-lg-12 text-center my-5">
                    <a className="btn btn-lg btn-danger" href="/JAMSTACK/index">REGRESAR</a>
                </div>
            </div>
        </Layout >
    )
}

export default CatalogPage
